





























export default {
	data() {
		return {
			 stories : []
		};
	},
	onLoad:function(){
		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/latest',
			method: 'GET',
			data(){},
			success: res => {
				console.log(res.data.stories);
				this.stories = res.data.stories;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods:{
		openinfo(e){
			
			var newsid = e.currentTarget.dataset.newsid;
			console.log(newsid);
			uni.navigateTo({
				url: '../info/info?id='+newsid					
			})
		}
	},
}



